








































import Vue from "vue";

declare global {
  interface Window {
    gtag: any;
    _storage: any;
  }
}

export default Vue.extend({
  name: "FormLabelSearch",
  data: function() {
    return {
      hits: [] as string[][],
      isAutocompleteActive: false,
      hasAutocompleteBlurListener: false,
      autocompleteWorker: -1,
      autocompleteListener: null
    };
  },
  computed: {
    keyword: {
      get() {
        return this.$store.state.product;
      },
      set(newValue) {
        this.$store.dispatch("setProduct", newValue);
      }
    }
  },
  mounted(): void {
    // Fetch labels catalog if not fetched already
    if (!window._storage) {
      const d = document;
      const catalog = d.createElement("script");
      catalog.src = "https://cdn.jsdelivr.net/gh/frenchcooc/labelmaker-landing@1.3.1/static/addon.catalog.min.js";
      catalog.async = true;
      catalog.defer = true;
      d.body.appendChild(catalog);
    }

    // Initialize keyword with what's in the store
    if (this.$store.state.product) {
      this.keyword = this.$store.state.product;
    }
  },
  methods: {
    /**
     * Handle labels search
     **/

    onAutocompleteFocused(): void {
      if (this.keyword === "") {
        return;
      }

      if (this.hits.length) {
        if (this.hits.length === 1 && this.hits[0][1] === this.keyword) {
          this.hideAutocomplete();
          return;
        }

        this.showAutocomplete();
      }
    },

    onAutocompleteKeyed: function() {
      if (this.keyword === "") {
        this.hideAutocomplete();
        return;
      }

      this.showAutocomplete();
      this.autocompleteSearch();
    },

    onAutocompleteItemClicked: function(event: MouseEvent) {
      var element = (event.target || event.srcElement) as HTMLElement | null;
      var keyword = element && element.innerText.toString();

      this.keyword = keyword;
      this.hideAutocomplete();
    },

    onAutocompleteDismissed: function() {
      // dismissed = user click outside of it
      this.hideAutocomplete();
    },

    showAutocomplete: function() {
      this.isAutocompleteActive = true;

      if (this.hasAutocompleteBlurListener) {
        return;
      }

      const _this = this;
      document.addEventListener("click", function(event) {
        var element = (event.target || event.srcElement) as HTMLElement;

        if (!element.closest) {
          return;
        }

        var closest = element.closest(".autocomplete");
        if (!closest) {
          _this.onAutocompleteDismissed();
        }
      });

      this.hasAutocompleteBlurListener = true;
    },

    hideAutocomplete: function() {
      this.isAutocompleteActive = false;
    },

    newAutocompleteWorker: function(): number {
      const now = new Date().getTime();
      this.autocompleteWorker = now;
      return now;
    },

    newEscapedKeywords: function(text: string): string {
      return text.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"); // $& means the whole matched string
    },

    autocompleteSearch: function() {
      const index = this.getLabelsCatalog() as string[][];
      const keyword = this.keyword;
      const results = [] as string[][];
      const maxResults = 10;
      const regex = new RegExp(this.newEscapedKeywords(keyword), "gi");
      const worker = this.newAutocompleteWorker();

      for (let i = 0, n = index.length; i < n; i++) {
        if (this.autocompleteWorker !== worker) {
          return; // stop execution if the worker has changed
        }

        if (results.length >= maxResults) {
          break;
        }

        const item = index[i];
        const labelName = item[1];

        if (regex.test(labelName)) {
          results.push(item);
        }
      }

      this.hits = results;
    },

    /** Catalog */

    getLabelsCatalog(): string[][] {
      return (window._storage && window._storage.ALL_LABELS) || [];
    }
  }
});

/**
 * Polyfill for Element.closest()
 * see: https://developer.mozilla.org/en-US/docs/Web/API/Element/closest
 */

if (Element && !Element.prototype.matches) {
  Element.prototype.matches =
    // @ts-ignore
    Element.prototype.msMatchesSelector || Element.prototype.webkitMatchesSelector;
}

if (Element && !Element.prototype.closest) {
  Element.prototype.closest = function(s: string) {
    var el = this;

    do {
      if (el.matches(s)) return el;
      // @ts-ignore
      el = el.parentElement || el.parentNode;
    } while (el !== null && el.nodeType === 1);
    return null;
  };
}
