





import Vue from "vue";
import { mapState } from "vuex";
import FormCreateLabels from "./FormCreateLabels.vue";
import GoogFilePicker from "./GoogFilePicker.vue";

declare global {
  interface Window {
    gtag: any;
    _options: { path: string };
  }
}

const routes = {
  "/": FormCreateLabels,
  "/filepicker": GoogFilePicker
};

const Router = Vue.extend({
  mounted: function() {
    window.gtag("event", "user_event", { user_account: "free" });
  },
  computed: {
    ...mapState(["location"]),
    routedComponent() {
      window.gtag("config", "UA-136395985-5", { page_path: this.location });
      // @ts-ignore
      return routes[this.location];
    }
  }
});

export default Router;
