





























import Vue from "vue";
import { mapState } from "vuex";
import FormSpreadsheetPicker from "./FormSpreadsheetPicker.vue";
import FormLabelSearch from "./FormLabelSearch.vue";
import FormLabelDesign from "./FormLabelDesign.vue";

declare global {
  interface Window {
    gtag: any;
    _backend: any;
    _frontend: any;
    _logger: any;
  }
}

export default Vue.extend({
  name: "FormCreateLabels",
  components: { FormLabelDesign, FormLabelSearch, FormSpreadsheetPicker },
  data() {
    return {
      isMergeInProgress: false
    };
  },
  computed: {
    ...mapState(["spreadsheet", "sheet", "template", "product"]),
    isMergeDoable(): boolean {
      return this.spreadsheet.id != "" && this.sheet.title !== "" && this.product !== "" && this.template !== "";
    }
  },
  methods: {
    reloadUI: function() {
      window._backend.uiReloadModal();
    },

    closeUI: function() {
      window._frontend.close();
    },

    /**
     * Handle merge
     **/

    initializeMerge: function() {
      this.isMergeInProgress = true;

      var _this = this;
      setTimeout(function() {
        window._backend
          .withFailureHandler(_this.onMergeFailure)
          .withSuccessHandler(_this.onMergeSuccess)
          .uiStartMerge(_this.spreadsheet.id, _this.sheet.title, _this.product, _this.template);
      }, 800);
    },
    onMergeSuccess(data?: { metadata: { metrics: { labels: number } } }): void {
      this.isMergeInProgress = false;

      if (!data || !data.metadata) {
        window._logger(new Error("Merge did not returned metadata"));
        return;
      }

      window._logger("Merge succeeded");
      window.gtag("event", "merge", {
        event_category: "engagement",
        event_label: this.product,
        value: data.metadata.metrics.labels || 0
      });
    },
    onMergeFailure(data: any): void {
      this.isMergeInProgress = false;
      window._logger(data);
    }
  }
});
